@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin hover {
  &:hover,
  &:focus {
    @content;
  }
}

@mixin tablet {
  @media (max-width: $tablet-width) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: $mobile-width) {
    @content;
  }
}

